import styled from "styled-components";

const BackButtonStyle = styled.div`
  button {
    padding: 5px 7px;
    margin: 12px;
    color: #4094e3;
    background-color: white;
    border: 1px solid #4094e3;
    border-radius: 4px;
  }
  button:hover {
    background-color: #4094e3;
    color: white;
    cursor: pointer;
  }
`;

export { BackButtonStyle };
